//引入axios请求函数
import requests from "./request";


//请求写好之后要存储到仓库之中
//囧话请求
//自定义一个reqEmbarrassing函数进行对外暴露当函数进行调用就发请求
export const reqChannel = () => {
    //let appcode = 'ce0c0bc919f248c1b984fa2e18149fdf';
    /* location = '101280601' */
    //发请求   利用axios封装好的函数requests进行 发送请求  method表明 get请求  然后去mains进行暴露引用
    return requests({
        url: 'http://ali-news.showapi.com/channelList',
        method: 'get',
        headers: {
            'Authorization': 'appcode ce0c0bc919f248c1b984fa2e18149fdf',
        },
        params: {
            title: '',
            channel: '',
            channelId: '',

            // page: '',
            maxResult: '100',
            needHtml: 1,

            needContent: '0',
        },
    })
};